<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="onSearch"
                right-icon
                @keyup.enter="onSearch"
            >
                <div slot="action" disabled="loading" @click="onSearch"></div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix="icon" name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-row>
                <van-col span="12">
                    <van-field v-model="baseData.fWorkShop" label="" label-align="left" disabled/>
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.stockPlace"
                        label=""
                        label-align="left"
                        @keyup.enter="onSearchPlace"
                    />
                </van-col>
            </van-row>

            <van-row>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.safeKeeping"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showSafekeepingPicker = true"
                    />
                    <van-popup v-model="showSafekeepingPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="Safekeepings"
                            @cancel="showSafekeepingPicker = false"
                            @confirm="onSafekeepingConfirm"
                        />
                    </van-popup>
                </van-col>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.shipper"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showShipperPicker = true"
                    />
                    <van-popup v-model="showShipperPicker" position="bottom">
                        <van-picker show-toolbar :columns="shippers" @cancel="showShipperPicker = false" @confirm="onShipperConfirm" />
                    </van-popup>
                </van-col>
            </van-row>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.materialName" :name="item.id" v-for="item in materialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.materialNumber" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.FBatchNo" label="" disabled label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.fstockName" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field ref="stockPlace" v-model="item.fstockPlaceName" label="" label-align="left" disabled/>
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field disabled v-model="item.fAuxQtyFinish" label="" label-align="left" type="number" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.materialUnit" label="" label-align="left" disabled />
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="newActiveName" accordion>
                <van-collapse-item :title="item.materialName" :name="item.id" v-for="item in newMaterialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.materialNumber" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.FBatchNo" label="" disabled label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.fstockName" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field ref="stockPlace" v-model="item.fstockPlaceName" label="" label-align="left" disabled/>
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field disabled v-model="item.fAuxQtyFinish" label="" label-align="left" type="number" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.materialUnit" label="" label-align="left" disabled />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field required v-model="item.materialActualSum" label="" label-align="left" type="number" disabled/>
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.materialUnit" label="" label-align="left" disabled />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial"></van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div class="form-item">
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack"></van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit"></van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            barcodeNumber: '',
            baseData: {
                // type:'',
                // typeId:'',
                // salesWay:'',
                // salesWayId:'',
                // purpose:'',
                // dept:'',
                // deptId:'',
            },
            activeName: '',
            newActiveName:'',
            showSalesWayPicker: false,
            salesWays: [
                // { id: 1, text: 133 },
                // { id: 2, text: 13 }
            ],
            showShipperPicker: false,
            shippers: [
                // { id: 1, text: '' },
                // { id: 2, text: '' }
            ],
            showSafekeepingPicker: false,
            Safekeepings: [],
            materialList: [],
            newMaterialList: [],
            stackPlace: '',
            barcodeList:[]
        };
    },
    created() {
        this.showLoading('...');
        var data = this.$route.query.data;
        this.baseData.fDeptID = data[0].fDeptID;
        this.baseData.fdeptName = data[0].fdeptName;
        this.baseData.fEmpID = data[0].fEmpID;
        this.baseData.fempName = data[0].fempName;
        this.baseData.salesMan = data[0].salesMan;
        this.baseData.salesManId = data[0].salesManId;
        this.baseData.shipper = data[0].shipper;
        this.baseData.shipperId = data[0].shipperId;
        this.baseData.fWorkShop = data[0].fWorkShop;
        this.baseData.fWorkShopId = data[0].fWorkShopId;
        this.baseData.rwBillNo = data[0].rwBillNo;
        this.baseData.stockPlace = '';
        this.baseData.stockPlaceID = '';
        this.baseData.creatorId=android.getUserID();
        data.some((item, i) => {
            item.materialList.some((mate, j) => {
                this.materialList.push(mate);
            });
        });
        this.$api.get('/selectAllEmpInfo', {}, response => {
            Toast.clear();
            if (response.status >= 200 && response.status < 300) {
                if (response.data.success == 'true') {
                    this.shippers = response.data.data;
                    this.Safekeepings = response.data.data;
                    Toast.success('');
                } else {
                    Toast.fail(response.data.msg);
                    return;
                }
            } else {
                Toast.fail('');
                return;
            }
        });
    },
    // mounted() {
    //     this.$refs.stockPlace.focus();
    // },
    methods: {
        onSearchPlace(){
            if(this.baseData.stockPlace.length==0){
                return;
            }
            this.showLoading('...');
            this.$api.get("/searchStockPlaceInfo",{'stockplaceNumber':this.baseData.stockPlace},response=>{
                this.baseData.stockPlace='';
                Toast.clear();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        Toast.success("");
                        var data=JSON.parse(response.data.data);
                        this.baseData.stockPlace=data.text;
                        this.baseData.stockPlaceID=data.id;
                    } else{
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.fail("");
                    return;
                }
            });
        },
        showLoading(data) {
            Toast.loading({
                duration: 0,
                message: data,
                forbidClick: true
            });
        },
        onSubmit() {
            this.showLoading('...');
            this.$api.post(
                '/submitProductionReceipt',
                {
                    baseData: JSON.stringify(this.baseData),
                    materialList: JSON.stringify(this.newMaterialList),
                    barcodeList: JSON.stringify(this.barcodeList)
                },
                response => {
                    Toast.clear();
                    if (response.status >= 200 && response.status < 300) {
                        if (response.data.success == 'true') {
                            Toast.success('');
                            android.back();
                        } else {
                            Toast.fail(response.data.msg);
                            return;
                        }
                    } else {
                        Toast.fail('');
                        console.log(response);
                        return;
                    }
                }
            );
        },
        onBack() {
            this.$router.go(-1);
        },
        onSalesWayConfirm(data) {
            this.baseData.salesWay = data.text;
            this.baseData.salesWayId = data.id;
            this.showSalesWayPicker = false;
        },
        onShipperConfirm(data) {
            this.baseData.shipper = data.text;
            this.baseData.shipperId = data.id;
            this.showShipperPicker = false;
        },
        onSafekeepingConfirm(data) {
            this.baseData.safeKeeping = data.text;
            this.baseData.safeKeepingId = data.id;
            this.showSafekeepingPicker = false;
        },
        deleteMaterial() {
            this.newMaterialList.some((item, i) => {
                if (item.id == this.newActiveName) {
                    this.newMaterialList.splice(i, 1);
                    this.barcodeList.some((barcode,j)=>{
                        if(item.mNumber==barcode.materialNumber){
                            this.barcodeList.splice(i,1);
                        }
                    });
                    return true;
                }
            });
        },
        onSearch() {
            if(this.barcodeNumber.length==0){
                return;
            }
            var flag=true;
            this.barcodeList.some((item,i)=>{
                if(item.barcodeNumber==this.barcodeNumber){
                    this.barcodeNumber='';
                    Toast.fail('');
                    flag=false;
                    return;
                }
            });
            if(flag){
                this.$api.get('/searchBarcodeNumber', { BarcodeNumber: this.barcodeNumber,id:2 }, response => {
                    this.barcodeNumber='';
                    Toast.clear();
                    this.focusNextInput();
                    if (response.status >= 200 && response.status < 300) {
                        if (response.data.success == 'true') {
                            Toast.success("");
                            var materialInfoList=JSON.parse(response.data.msg);
                            this.materialList.some((item,i)=>{
                                if(item.stockPlace=="*"){
                                    item.stockPlace="";
                                }
                                materialInfoList.some((materialInfo,i)=>{
                                    var flag=true;
                                    this.newMaterialList.some((saleItem,i)=>{
                                        console.log(saleItem);
                                        console.log(materialInfo);
                                        if(saleItem.materialNumber==materialInfo.mNumber&&saleItem.fBatchNo==materialInfo.batch&&saleItem.fstockName==materialInfo.warehouse&&saleItem.stockPlace==materialInfo.position){
                                            saleItem.materialActualSum=Number(saleItem.materialActualSum)+Number(materialInfo.mSum);
                                            flag=false;
                                            this.newActiveName=saleItem.id;
                                            this.barcodeList.push(materialInfo);
                                        }
                                    });
                                    if(flag){
                                        let saleMate=JSON.parse(JSON.stringify(item));
                                        saleMate.id=this.newMaterialList.length+1;
                                        saleMate.materialActualSum=Number(materialInfo.mSum);
                                        saleMate.stockPlace=this.baseData.stockPlace;
                                        saleMate.stockPlaceId=this.baseData.stockPlaceID==''?'0':this.baseData.stockPlaceID;
                                        this.newMaterialList.push(saleMate);
                                        this.newActiveName=saleMate.id;
                                        materialInfo.position=this.baseData.stockPlace;
                                        materialInfo.positionId=this.baseData.stockPlaceID==''?'0':this.baseData.stockPlaceID;
                                        this.barcodeList.push(materialInfo);
                                    }
                                });
                            });
                        } else {
                            Toast.clear();
                            Toast.fail(response.data.msg);
                            this.barcodeNumber = '';
                            return;
                        }
                    } else {
                        Toast.clear();
                        Toast.fail('');
                        this.barcodeNumber = '';
                        return;
                    }
                });
            }
            // this.nextTick();
        },

        openScanner() {
            android.openScanner();
        },
        scannerSearch(data) {
            this.barcodeNumber = data;
            this.onSearch();
        },
        focusNextInput() {
            var inputs = document.getElementsByTagName('input');
            inputs[0].focus();
        },
        nextTick() {
            this.$nextTick(() => {
                this.$refs.stockPlace.focus();
            });
        }
    }
};
</script>



