<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="onSearch"
                right-icon
                @keyup.enter="onSearch"
            >
                <div slot="action" disabled="loading" @click="onSearch"></div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix="icon" name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.fposname"
                        label=""
                        disabled
                        label-align="left"
                        readonly
                    />
                </van-col>
                <van-col span="12">
                    <van-field v-model="baseData.fsupplyname" label="" disabled label-align="left" label-width="2rem" />
                </van-col>
            </van-row>

            <van-row>
                <van-col span="12">
                    <van-field v-model="baseData.fmateNum" label="" disabled label-align="left" label-width="2rem" />
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.fmateName"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                    />
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field v-model="baseData.fBatchNo" label="" disabled label-align="left" label-width="2rem" />
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.fcheckSum"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                    />
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.shipper"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showShipperPicker = true"
                    />
                    <van-popup v-model="showShipperPicker" position="bottom">
                        <van-picker show-toolbar :columns="shippers" @cancel="showShipperPicker = false" @confirm="onShipperConfirm" />
                    </van-popup>
                </van-col>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.safeKeeping"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showSafekeepingPicker = true"
                    />
                    <van-popup v-model="showSafekeepingPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="Safekeepings"
                            @cancel="showSafekeepingPicker = false"
                            @confirm="onSafekeepingConfirm"
                        />
                    </van-popup>
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.passSum"
                        disabled
                        label=""
                        label-align="left"
                    />
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.ngSum"
                        label=""
                        label-align="left"
                        disabled
                    />
                </van-col>
            </van-row>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-row style="margin:0.3rem">
                <van-col span="24">
                    <van-uploader v-model="fileList" multiple/>
                </van-col>
            </van-row>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.qcName" :name="item.qcEntry" v-for="item in QCBaseData" :key="item.qcEntry">
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.qcNumber" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.qcName" label="" disabled label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="24">
                            <van-field v-model="item.isOk" label="" disabled required label-align="left" @click="showIsOkPicker=true"/>
                            <van-popup v-model="showIsOkPicker" position="bottom">
                                <van-picker
                                    show-toolbar
                                    :columns="columns"
                                    @cancel="showIsOkPicker = false"
                                    @confirm="onConfirm"
                                />
                            </van-popup>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="newActiveName" accordion>
                <van-collapse-item :title="item.materialName" :name="item.id" v-for="item in newMaterialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.materialNumber" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.fBatchNo" label="" disabled label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.NbarcodeNumber" label="" label-align="left" disabled />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.isOk" label="" label-align="left" disabled required @click="showMIsOkPicker = true"/>
                            <van-popup v-model="showMIsOkPicker" position="bottom">
                                <van-picker
                                    show-toolbar
                                    :columns="columns"
                                    @cancel="showMIsOkPicker = false"
                                    @confirm="onMConfirm"
                                />
                            </van-popup>
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial"></van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div class="form-item">
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack"></van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit"></van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
import { Uploader } from 'vant';
import axios from 'axios';
export default {
    data() {
        return {
            barcodeNumber: '',
            NbarcodeNumber: '',
            baseData: {
                // type:'',
                // typeId:'',
                // salesWay:'',
                // salesWayId:'',
                // purpose:'',
                // dept:'',
                // deptId:'',
            },
            QCBaseData:{

            },
            activeName: '',
            newActiveName:'',
            showSalesWayPicker: false,
            salesWays: [
                // { id: 1, text: 133 },
                // { id: 2, text: 13 }
            ],
            showShipperPicker: false,
            shippers: [
                // { id: 1, text: '' },
                // { id: 2, text: '' }
            ],
            showSafekeepingPicker: false,
            Safekeepings: [],
            materialList: [],
            newMaterialList: [],
            barcodeList: [],
            columns: ['', ''],
            showIsOkPicker:false,
            showMIsOkPicker:false,
            showShipperPicker: false,
            shippers: [
                // { id: 1, text: '' },
                // { id: 2, text: '' }
            ],
            showSafekeepingPicker: false,
            Safekeepings: [],
            fileList: [
            ],
        };
    },
    created() {
        this.showLoading('...');
        var data = this.$route.query.data;
        var mate = this.$route.query.mate;
        this.baseData.fposname = '';
        this.baseData.fSupplyID = data[0].fSupplyID;
        this.baseData.fsupplyname = data[0].fsupplyname;
        this.baseData.fmateNum = mate.materialNumber;
        this.baseData.fmateName = mate.materialName;
        this.baseData.fBatchNo = mate.fBatchNo;
        console.log(mate.fQty);
        this.baseData.fcheckSum = Number(mate.fQty);
        this.baseData.passSum = 0;
        this.baseData.ngSum = 0;
        this.baseData.fBillNo=data[0].fBillNo;
        this.baseData.fBillId = android.getUserID();
        this.materialList.push(mate);
        this.$api.get('/searchQCJCX', {mateNumber:mate.materialNumber}, response => {
            Toast.clear();
            if (response.status >= 200 && response.status < 300) {
                if (response.data.success == 'true') {
                    console.log(response.data.data);
                    this.QCBaseData = JSON.parse(response.data.data);
                    Toast.success('');
                } else {
                    Toast.fail(response.data.msg);
                    return;
                }
            } else {
                Toast.fail('');
                return;
            }
        });
        this.$api.get('/selectAllEmpInfo', {}, response => {
            Toast.clear();
            if (response.status >= 200 && response.status < 300) {
                if (response.data.success == 'true') {
                    this.shippers = response.data.data;
                    this.Safekeepings = response.data.data;
                    Toast.success('');
                } else {
                    Toast.fail(response.data.msg);
                    return;
                }
            } else {
                Toast.fail('');
                return;
            }
        });
    },
    computed:{
        newNgSum:function(){
            return this.baseData.ngSum;
        }
    },
    watch:{
        newNgSum:{
            deep: true,
            handler: function (newVal,oldVal){
                console.log(1111);
                this.baseData.passSum=Math(this.baseData.fcheckSum-oldVal);
            }
        }
    },
    methods: {
        showLoading(data) {
            Toast.loading({
                duration: 0,
                message: data,
                forbidClick: true
            });
        },
        onSubmit() {
            this.showLoading('...');
            if(this.newMaterialList.length!=Number(this.baseData.fcheckSum)){
                Toast.fail("");
                return;
            }
            var f=false;
            this.newMaterialList.some((item,i)=>{
                if(item.isOk==undefined){
                    f=true;
                    Toast.fail(item.NbarcodeNumber+"");
                    return;
                }
            });
            this.QCBaseData.some((item,i)=>{
                if(item.isOk==undefined){
                    f=true;
                    Toast.fail(item.qcName+"");
                    return;
                }
            });
            if(f){
                return;
            }
            var list=[];
            this.fileList.some((item,i)=>{
                var file={};
                file.content=item.content;
                file.fileName=item.file.name;
                file.size=item.file.size;
                list.push(file);
            });
            this.$api.post(
                '/submitQCLL',
                {
                    baseData: JSON.stringify(this.baseData),
                    materialList: JSON.stringify(this.newMaterialList),
                    QCBaseData: JSON.stringify(this.QCBaseData),
                    content:JSON.stringify(list)
                },
                response => {
                    Toast.clear();
                    if (response.status >= 200 && response.status < 300) {
                        if (response.data.success == 'true') {
                            Toast.success('');
                            android.back();
                        } else {
                            Toast.fail(response.data.msg);
                            return;
                        }
                    } else {
                        Toast.fail('');
                        console.log(response);
                        return;
                    }
                }
            );
        },
        onBack() {
            this.$router.go(-1);
        },
        onSalesWayConfirm(data) {
            this.baseData.salesWay = data.text;
            this.baseData.salesWayId = data.id;
            this.showSalesWayPicker = false;
        },
        onShipperConfirm(data) {
            this.baseData.shipper = data.text;
            this.baseData.shipperId = data.id;
            this.showShipperPicker = false;
        },
        onSafekeepingConfirm(data) {
            this.baseData.safeKeeping = data.text;
            this.baseData.safeKeepingId = data.id;
            this.showSafekeepingPicker = false;
        },
        deleteMaterial() {
            this.newMaterialList.some((item, i) => {
                if (item.id == this.newActiveName) {
                    this.newMaterialList.splice(i, 1);
                    if(item.isOk==''){
                        this.baseData.passSum=Number(this.baseData.passSum)-1;
                    }else if(item.isOk==''){
                        this.baseData.ngSum=Number(this.baseData.ngSum)-1;
                    }
                    this.barcodeList.some((barcode,j)=>{
                        if(item.NbarcodeNumber==barcode.barcodeNumber){
                            this.barcodeList.splice(i,1);
                        }
                    });
                    return true;
                }
            });
        },
        onSearch() {
            if (this.barcodeNumber.length == 0) {
                return;
            }
            var flag = true;
            this.barcodeList.some((item, i) => {
                if (item.barcodeNumber == this.barcodeNumber) {
                    Toast.fail('');
                    flag = false;
                    return;
                }
            });
            // this.submitSum();
            if (flag) {
                this.$api.get("/searchBarcodeNumber",{BarcodeNumber:this.barcodeNumber,id:'2'},response=>{
                    this.barcodeNumber='';
                    this.focusNextInput();
                    if (response.status >= 200 && response.status < 300) {
                        if(response.data.success=="true"){
                            Toast.success("");
                            var materialInfoList=JSON.parse(response.data.msg);
                            materialInfoList.some((materialInfo,i)=>{
                                console.log(materialInfo);
                                if(materialInfo.typeNumber==this.baseData.fBillNo){
                                    let saleMate=JSON.parse(JSON.stringify(materialInfo));
                                    saleMate.id=this.newMaterialList.length+1;
                                    saleMate.NbarcodeNumber=materialInfo.barcodeNumber;
                                    saleMate.fBatchNo=materialInfo.batch;
                                    saleMate.materialNumber=materialInfo.mNumber;
                                    saleMate.materialName=materialInfo.mName;
                                    this.newMaterialList.push(saleMate);
                                    this.newActiveName=saleMate.id;
                                    this.barcodeList.push(materialInfo);
                                }
                            });
                        } else {
                            Toast.clear();
                            Toast.fail(response.data.msg);
                            return;
                        }
                    } else {
                        Toast.clear();
                        Toast.fail("");
                        return;
                    }
                });
            }
        },

        openScanner() {
            android.openScanner();
        },
        scannerSearch(data) {
            this.barcodeNumber = data;
            this.onSearch();
        },
        onSearchPlace(){
            if(this.baseData.stockPlace.length==0){
                return;
            }
            this.showLoading('...');
            this.$api.get("/searchStockPlaceInfo",{'stockplaceNumber':this.baseData.stockPlace},response=>{
                this.baseData.stockPlace='';
                Toast.clear();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        Toast.success("");
                        var data=JSON.parse(response.data.data);
                        this.baseData.stockPlace=data.text;
                        this.baseData.stockPlaceID=data.id;
                    } else{
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.fail("");
                    return;
                }
            });
        },
        focusNextInput() {
            var inputs = document.getElementsByTagName('input');
            inputs[0].focus();
        },
        // submitSum() {
        //     let sumMaterialList = this.newMaterialList;
        //     console.log(1);
        //     console.log(sumMaterialList);
        // }
        onConfirm(value) {
            this.showIsOkPicker=false;
            this.QCBaseData.some((item,i)=>{
                if(this.activeName==item.qcEntry){
                    this.$set(item,'isOk',value);
                    return;
                }
            });
        },
        onMConfirm(value) {
            this.showMIsOkPicker=false;
            this.newMaterialList.some((item,i)=>{
                if(this.newActiveName==item.id){
                    if(""==value){
                        if(item.isOk==""){

                        }
                        if(item.isOk==""){
                            this.baseData.passSum=Number(this.baseData.passSum)+1;
                            this.baseData.ngSum=Number(this.baseData.ngSum)-1;
                        }
                        if(item.isOk==undefined){
                            this.baseData.passSum=Number(this.baseData.passSum)+1;
                        }
                    }else if(""==value){
                        if(item.isOk==""){
                            this.baseData.passSum=Number(this.baseData.passSum)-1;
                            this.baseData.ngSum=Number(this.baseData.ngSum)+1;
                        }
                        if(item.isOk==""){

                        }
                        if(item.isOk==undefined){
                            this.baseData.ngSum=Number(this.baseData.ngSum)+1;
                        }
                    }
                    this.$set(item,'isOk',value);
                    return;
                }
            });
        }
    }
};
</script>



